___CSS_LOADER_EXPORT___.push([module.id, `
.do-list-add-task-btn {
    border: none;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    gap: 20px;
    font-size: 1rem;
    cursor: pointer;
    background-color: white;
    padding: 5px 10px;
    transition: background-color 300ms;
    border-radius: 8px;
    width: 25vw;
}
.do-list-add-task-btn:hover {
    background-color: rgba(128, 128, 128, 0.25);
    transition: background-color 300ms;
}
.do-list-add-task-btn:active {
    transition: background-color 300ms;
    background-color: rgba(128, 128, 128, 0.35);;
}
.do-add-btn {
    width: 1.25rem;
    height: 1.25rem;
}
@media only screen and (max-width: 600px) {
    .do-list-add-task-btn {
        width: 100%;
    }
}
@media only screen and (min-width:600px) and (max-width: 961px) { 
    /* tablet, landscape iPad, lo-res laptops ands desktops */ 
    .do-list-add-task-btn {
        width: 100%;
    }
    .do-add-text {
        font-size: 1.3rem;
    }
    .do-add-btn {
        width: 25px;
        height: 25px;
    }
}
@media only screen and (min-width:961px) and (max-width: 1025px) { 
    /* big landscape tablets, laptops, and desktops */ 
    .do-list-add-task-btn {
        width: 100%;
    }
    .do-add-text {
        font-size: 1.5rem;
    }
    .do-add-btn {
        width: 30px;
        height: 30px;
    }
}
@media (min-width:1281px) {
    /* hi-res laptops and desktops */
    .do-list-add-task-btn {
        width: 100%;
        font-size: 1.3rem;
        padding-top: 10px;
        padding-bottom: 10px;
        font-weight: 500;
    }
    .do-add-btn {
        width: 29px;
        height: 29px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/list-add-btn.css"],"names":[],"mappings":";AACA;IACI,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;IACT,eAAe;IACf,eAAe;IACf,uBAAuB;IACvB,iBAAiB;IACjB,kCAAkC;IAClC,kBAAkB;IAClB,WAAW;AACf;AACA;IACI,2CAA2C;IAC3C,kCAAkC;AACtC;AACA;IACI,kCAAkC;IAClC,2CAA2C;AAC/C;AACA;IACI,cAAc;IACd,eAAe;AACnB;AACA;IACI;QACI,WAAW;IACf;AACJ;AACA;IACI,yDAAyD;IACzD;QACI,WAAW;IACf;IACA;QACI,iBAAiB;IACrB;IACA;QACI,WAAW;QACX,YAAY;IAChB;AACJ;AACA;IACI,iDAAiD;IACjD;QACI,WAAW;IACf;IACA;QACI,iBAAiB;IACrB;IACA;QACI,WAAW;QACX,YAAY;IAChB;AACJ;AACA;IACI,gCAAgC;IAChC;QACI,WAAW;QACX,iBAAiB;QACjB,iBAAiB;QACjB,oBAAoB;QACpB,gBAAgB;IACpB;IACA;QACI,WAAW;QACX,YAAY;IAChB;AACJ","sourcesContent":["\n.do-list-add-task-btn {\n    border: none;\n    display: flex;\n    flex-direction: row;\n    justify-content: start;\n    align-items: center;\n    gap: 20px;\n    font-size: 1rem;\n    cursor: pointer;\n    background-color: white;\n    padding: 5px 10px;\n    transition: background-color 300ms;\n    border-radius: 8px;\n    width: 25vw;\n}\n.do-list-add-task-btn:hover {\n    background-color: rgba(128, 128, 128, 0.25);\n    transition: background-color 300ms;\n}\n.do-list-add-task-btn:active {\n    transition: background-color 300ms;\n    background-color: rgba(128, 128, 128, 0.35);;\n}\n.do-add-btn {\n    width: 1.25rem;\n    height: 1.25rem;\n}\n@media only screen and (max-width: 600px) {\n    .do-list-add-task-btn {\n        width: 100%;\n    }\n}\n@media only screen and (min-width:600px) and (max-width: 961px) { \n    /* tablet, landscape iPad, lo-res laptops ands desktops */ \n    .do-list-add-task-btn {\n        width: 100%;\n    }\n    .do-add-text {\n        font-size: 1.3rem;\n    }\n    .do-add-btn {\n        width: 25px;\n        height: 25px;\n    }\n}\n@media only screen and (min-width:961px) and (max-width: 1025px) { \n    /* big landscape tablets, laptops, and desktops */ \n    .do-list-add-task-btn {\n        width: 100%;\n    